import { useI18next } from "gatsby-plugin-react-i18next";
import * as React from "react";
import "./LocaleChanger.scss";

export const LocaleChanger = () => {

    const i18n = useI18next();

    const locales = [
        {
            iso: "en",
            name: "English"
        },
        {
            iso: "zh-CHT",
            name: ""
        },
        {
            iso: "zh-CHS",
            name: ""
        },
        {
            iso: "ja",
            name: ""
        }
    ]

    const changeLocale = (langString) => {
        i18n.changeLanguage(langString);
    }

    return (
        <div className="locale-changer flex-row">
            {
                locales.map(locale => {
                    return (
                        <button className={`locale-changer-button ${i18n.language == locale.iso ? "locale-active" : ""}`} onClick={() => changeLocale(locale.iso)}>{locale.name}</button>
                    )
                })
            }
        </div>
    )
}