import * as React from "react";
import { Trans, useTranslation } from "react-i18next";
import IntroImageRef from "../../assets/backdrop/intro-image.jpg";
import Choice from "../../assets/icons/choice.svg";
import Deck from "../../assets/icons/deck.svg";
import Rogue from "../../assets/icons/rogue.svg";
import Scenario from "../../assets/icons/scenario.svg";
import Simulation from "../../assets/icons/simulation.svg";
import Survival from "../../assets/icons/survival.svg";
import { FeatureIcon } from "../../components/FeatureIcon/FeatureIcon";
import "./IntroSection.scss";

export const IntroSection = () => {

    const { t, i18n } = useTranslation();

    const featureConfig = [
        [Rogue, "intro.feature-rogue", "Rogue-like"],
        [Choice, "intro.feature-choices", "Choices"],
        [Survival, "intro.feature-survival", "Survival"],
        [Scenario, "intro.feature-scenarios", "Scenarios"],
        [Deck, "intro.feature-deck", "Deck-builder"],
        [Simulation, "intro.feature-simulation", "Simulation"],
    ]

    return (
        <div className="intro-section">
            <div className="intro-section-background" style={
                {
                    backgroundImage: `linear-gradient(
                        rgba(9, 7, 12, 1),
                        rgba(9, 7, 12, 0.8),
                        rgba(9, 7, 12, 0.5),
                        rgba(9, 7, 12, 0.25),
                        rgba(9, 7, 12, 0.5), 
                        rgba(9, 7, 12, 0.8), 
                        rgba(9, 7, 12, 1)
                      ),
                      url(${IntroImageRef})`
                }
            }>
                <div className="intro-section-content-wrapper flex-column">
                    <div className={`intro-section-content-title ${i18n.language}`}>
                        {t("intro.title")}
                    </div>
                    <div className="intro-section-content-caption">
                        <Trans i18nKey={"intro.caption"}>
                            To escape from zombies at 8, and start to <span className="red-text">work</span> at 9
                        </Trans>
                    </div>
                    <div className="intro-section-content-desc">
                        <Trans i18nKey={"intro.desc"}>
                            Codename: Apocalypse is a <span className="red-text">rogue-like</span> deck-building game that combines <span className="red-text">role-playing</span> and <span className="red-text">deck-building</span> elements as one. Play as a normal civilian and try to live as usual, while an unknown doomsday crisis is approaching.
                        </Trans>
                    </div>

                    <div className="feature-icon-container flex-row">
                        {
                            featureConfig.map(([svg, text, fallback]) => <FeatureIcon svg={svg} text={t(text, fallback)} />)
                        }
                    </div>
                </div>
            </div>
        </div >
    );
}