import "@fontsource/averia-serif-libre";
import "@fontsource/black-ops-one";
import "@fontsource/noto-serif-sc";
import "@fontsource/noto-serif-tc";
import "@fontsource/tajawal";
import { graphql } from "gatsby";
import * as React from "react";
import Helmet from "react-helmet";
import { ParallaxProvider } from "react-scroll-parallax";
import { LocaleChanger } from "../components/LocaleChanger/LocaleChanger";
import { FooterSection } from "../sections/FooterSection/FooterSection";
import { GallerySection } from "../sections/GallerySection/GallerySection";
import { HeroSection } from "../sections/HeroSection/HeroSection";
import { InfoSection } from "../sections/InfoSection/InfoSection";
import { IntroSection } from "../sections/IntroSection/IntroSection";
import { TeamSection } from "../sections/TeamSection/TeamSection";
import { VideoSection } from "../sections/VideoSection/VideoSection";
import "./index.scss";

// markup
const IndexPage = () => {
  return (
    <ParallaxProvider>
      <main>
        <Helmet>
          <meta charSet="utf-8" />
          <meta name="description" content="A deck-building roguelike survival video game that synthesizes a meaningful narration." />
          <meta name="keywords" content="Roguelike, Roguelite, Deckbuilder, Video Games, Games" />
          <meta name="author" content="Rank B" />
          <meta name="viewport" content="width=device-width, initial-scale=1.0" />

          <meta property="og:title" content="Codename: Apocalypse | A Roguelike Deckbuilder" />
          <meta property="og:type" content="website" />
          <meta property="og:locale" content="en_US" />
          <meta property="og:url" content="https://rankbdev.github.io/codea/" />
          <meta property="og:site_name" content="Codename: Apocalypse" />
          <meta property="og:description" content="A deck-building roguelike survival video game that synthesizes a meaningful narration." />
          <meta property="og:image" content="https://rankbdev.github.io/codea/thumbnail.jpg" />

          <title>Codename: Apocalypse</title>
        </Helmet>
        <LocaleChanger />
        <HeroSection />
        <IntroSection />
        <GallerySection />
        <VideoSection />
        <TeamSection />
        <InfoSection />
        <FooterSection />
      </main>
    </ParallaxProvider>
  )
}

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: {language: {eq: $language}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;

export default IndexPage
