import { faFileLines } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import addToMailchimp from 'gatsby-plugin-mailchimp';
import * as React from "react";
import { useRef } from "react";
import { Trans, useTranslation } from "react-i18next";
import { SocialIcons } from "../HeroSection/HeroSection";
import "./InfoSection.scss";

export const InfoSection = () => {

    const { t, i18n } = useTranslation();

    const handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            buttonRef.current.click();
        }
    }

    const onSubmit = async (e) => {
        e.preventDefault();
        const email = inputRef.current.value;

        if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email)) {
            const result = await addToMailchimp(email, "7130357");

            if (result)
                alert(result.msg);
            else
                alert(t("info.error"));
        }
        else {
            alert(t("info.please-enter-valid-email"));
        }
    }

    const inputRef = useRef();
    const buttonRef = useRef();

    return (
        <div className="info-section">
            <div className="hero-section-coming-soon">
                <Trans i18nKey={"hero.coming-soon"}>
                    COMING SOON... <span className="red-text">2022</span>
                </Trans>
            </div>

            <div className="info-section-info-wrapper">
                <div className="info-section-info">
                    <Trans i18nKey={"info.game-title"}>
                        <span className="red-text">Game Title:</span> Codename: Apocalypse
                    </Trans>
                </div>
                <div className="info-section-info">
                    <Trans i18nKey={"info.genre"}>
                        <span className="red-text">Genre:</span> Deck-building Roguelike Survival Game
                    </Trans>
                </div>
                <div className="info-section-info">
                    <Trans i18nKey={"info.distribution"}>
                        <span className="red-text">Distributing Platform:</span> Steam, itch.io
                    </Trans>
                </div>
                <div className="info-section-info">
                    <Trans i18nKey={"info.number-of-players"}>
                        <span className="red-text">Number of Players:</span> Single-player
                    </Trans>
                </div>
                <div className="info-section-info">
                    <Trans i18nKey={"info.os"}>
                        <span className="red-text">Operating System:</span> Windows
                    </Trans>
                </div>
            </div>

            <div className="info-section-newsletter-wrapper">
                <div className="info-section-newsletter-title">
                    {t("info.subscription")}
                </div>
                <form className="info-section-newsletter-content-wrapper">
                    <input ref={inputRef} onKeyPress={handleKeyPress} required className="info-section-newsletter-input" type="email" placeholder={t("info.placeholder")} pattern="/^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/" />
                    <button ref={buttonRef} onClick={onSubmit} className="info-section-newsletter-submit-button">{t("info.submit")}</button>
                </form>
            </div>

            <div className="info-section-press-kit-desc">
                {t("info.press-kit")}
            </div>

            <a href="https://sites.google.com/view/rankbcodea/" target="_blank">
                <FontAwesomeIcon className="hero-section-social-media-icon" icon={faFileLines} color="white" size="2x" />
            </a>

            <div className="hero-section-coming-soon-desc">
                {t("info.follow-us")}
            </div>

            <SocialIcons />

        </div >
    );
}