import React, { useState } from "react";
import ImgsViewer from "react-images-viewer";
import { GalleryCard } from "../../components/GalleryCard/GalleryCard";
import "./GallerySection.scss";

import { useTranslation } from "react-i18next";
import i0 from "../../assets/gallery/image00.jpg";
import i1 from "../../assets/gallery/image01.jpg";
import i2 from "../../assets/gallery/image02.jpg";
import i3 from "../../assets/gallery/image03.jpg";
import i4 from "../../assets/gallery/image04.jpg";
import i5 from "../../assets/gallery/image05.jpg";
import i6 from "../../assets/gallery/image06.jpg";
import i7 from "../../assets/gallery/image07.jpg";


export const GallerySection = () => {

    const { t, i18n } = useTranslation();

    const galleryArray = [
        {
            src: i0,
            caption: "gallery.img-1",
            fallback: "Scenario Selection UI",
        },
        {
            src: i1,
            caption: "gallery.img-2",
            fallback: "Home Scene UI",
        },
        {
            src: i2,
            caption: "gallery.img-3",
            fallback: "Routine Selection UI",
        },
        {
            src: i3,
            caption: "gallery.img-4",
            fallback: "Battle UI",
        },
        {
            src: i4,
            caption: "gallery.img-5",
            fallback: "Event UI",
        },
        {
            src: i5,
            caption: "gallery.img-6",
            fallback: "Concept arts of side characters",
        },
        {
            src: i6,
            caption: "gallery.img-7",
            fallback: "Concept arts of side characters",
        },
        {
            src: i7,
            caption: "gallery.img-8",
            fallback: "Concept arts of side characters",
        },
    ]

    const [currImg, setCurrImg] = useState(0);
    const [viewerIsOpen, setViewerIsOpen] = useState(false);

    const openViewer = (imgNo) => {
        setViewerIsOpen(true);
        setCurrImg(imgNo);
    }

    const closeViewer = () => setViewerIsOpen(false);

    const onClickPrev = () => {
        if (currImg > 0) {
            setCurrImg(currImg - 1);
        }
        else {
            setCurrImg(galleryArray.length - 1);
        }
    }

    const onClickNext = () => {
        if (currImg <= galleryArray.length - 1) {
            setCurrImg(currImg + 1);
        }
        else {
            setCurrImg(0);
        }
    }

    return (
        <>
            <div className="gallery-section">
                <div className="gallery-section-content-wrapper flex-column">
                    <div className={`gallery-section-content-title ${i18n.language}`}>
                        {t("gallery.title")}
                    </div>
                    <div className="gallery-section-content-grid-layout flex-row">
                        {
                            galleryArray.map((img, idx) => <GridItem src={img.src} caption={t(img.caption, img.fallback)} onClick={() => openViewer(idx)} />)
                        }
                    </div>
                </div>
                <div className="gallery-section-disclaimer">
                    {t("gallery.subject-to-change")}
                </div>
            </div>

            <ImgsViewer
                imgs={galleryArray.map((img) => { return { src: img.src, caption: t(img.caption, img.fallback) } })}
                currImg={currImg}
                isOpen={viewerIsOpen}
                onClose={closeViewer}
                onClickPrev={onClickPrev}
                onClickNext={onClickNext}
                backdropCloseable
            />
        </>
    );
}

const GridItem = ({ src, onClick }) => (
    <div className="gallery-grid-item">
        <GalleryCard src={src} onClick={onClick} />
    </div>
)