import { faFacebookSquare, faInstagramSquare, faItchIo, faSteamSquare, faTwitterSquare } from '@fortawesome/free-brands-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import * as React from "react";
import { Trans, useTranslation } from 'react-i18next';
import { Parallax } from 'react-scroll-parallax';
import BuildingsRef from "../../assets/backdrop/Building.png";
//import HeroImageRef from "../../assets/backdrop/hero-image.jpg";
import FrontObjectRef from "../../assets/backdrop/FRONT_Object.png";
import SkyRef from "../../assets/backdrop/sky.jpg";
import DiscordIcon from "../../assets/icons/Discord.png";
import MouseIcon from "../../assets/icons/mouse.svg";
import LogoRef from "../../assets/Logo.png";
import "./HeroSection.scss";

export const HeroSection = () => {

    const { t, i18n } = useTranslation();

    return (
        <div className="hero-section">
            <div className="hero-section-background">


                <div className="hero-section-para-sky" style={
                    {
                        backgroundImage: `url(${SkyRef})`
                    }
                }>
                </div>


                <Parallax speed={-27}>
                    <img className="hero-section-para-building" src={BuildingsRef} />
                </Parallax>

                <div className="hero-section-front-object" style={
                    {
                        backgroundImage: `linear-gradient(
                            rgba(9, 7, 12, 0),
                            rgba(9, 7, 12, 0) 85%,
                            rgba(9, 7, 12, 0.75) 90%, 
                            rgba(9, 7, 12, 1) 100%
                        ),
                      url(${FrontObjectRef})`
                    }
                }>
                </div>

                <div className="flex-column hero-section-title-wrapper">
                    {/* <div className="hero-section-game-desc">
                        <span>A roguelike deckbuilder featuring a pre-</span>
                        <span className="red-text"><b>apocalypse </b></span>
                        <span>narrative experience</span>
                    </div> */}
                    <img className="hero-section-game-title" src={LogoRef} />

                    {/* <div className="hero-section-codename">CODENAME</div>
                        <div className="hero-section-colon">:</div>
                        <div className="red-text">APOCALYPSE</div> */}

                    <div className={`hero-section-game-desc ${i18n.language}`}>
                        {/* An unknown <span className="red-text"><b>apocalypse</b></span> is approaching, and the end is nigh */}
                        <Trans i18nKey={"hero.game-desc"} >
                            An unknown <span className="red-text">apocalypse</span> is approaching, and the end is nigh
                        </Trans>
                    </div>

                    <div style={{ height: "120px" }}></div>
                </div>
                <div className="hero-section-social flex-column">
                    <div className="hero-section-coming-soon">
                        <Trans i18nKey={"hero.coming-soon"}>
                            COMING SOON... <span className="red-text">2022</span>
                        </Trans>
                    </div>
                    <SocialIcons />

                    <MouseIcon className="mouse-scroll-icon" viewBox="0 0 202 459" width="30" height="67.5" />
                </div>
            </div>
        </div>
    )
}

export const SocialIcons = () => (
    <div className="hero-section-social-media-container flex-row">
        <a href="https://www.facebook.com/profile.php?id=100075844770182" target="_blank">
            <FontAwesomeIcon className="hero-section-social-media-icon" icon={faFacebookSquare} color="white" size="2x" />
        </a>
        <a href="https://www.instagram.com/codename_apocalypse/" target="_blank">
            <FontAwesomeIcon className="hero-section-social-media-icon" icon={faInstagramSquare} color="white" size="2x" />
        </a>
        <a href="https://twitter.com/RankB_Dev" target="_blank">
            <FontAwesomeIcon className="hero-section-social-media-icon" icon={faTwitterSquare} color="white" size="2x" />
        </a>
        <a href="https://discord.gg/yqZXhKf9AH" target="_blank">
            <img src={DiscordIcon} className="hero-section-social-media-icon" width="28" height="28" style={{ marginTop: 5 }} />
        </a>
        <a href="https://rankb.itch.io/codea" target="_blank">
            <FontAwesomeIcon className="hero-section-social-media-icon" icon={faItchIo} color="white" size="2x" />
        </a>
        <a href="https://store.steampowered.com/app/2051290/Codename_Apocalypse/" target="_blank">
            <FontAwesomeIcon className="hero-section-social-media-icon" icon={faSteamSquare} color="white" size="2x" />
        </a>
    </div>
)